		code: `/*
       type alias.    ""  Student.
      Teacher  Director   .   
       .    .
    
    Teacher  , ,       .
       Teacher-   id      ,
       .

    Director  , ,     . 
      id        .
       id     , , 
      
*/

type User = {
    id: string; //  
    name: string; //  
}

type Course = {
    id: number; //  
    title: string; //  
}

type WithRate = {
    rate: 1 | 2 | 3 | 4 | 5; //    
}

type WithStudentRole = {
    role: "student"; //  : 
}

type WithTeacherRole = {
    role: "teacher"; //  : 
}

type WithLevel = {
    level: "junior" | "middle" | "senior"; //    
}

/* --- */

//   StudentCourse    Course, WithStudentRole, WithRate  WithLevel.
type StudentCourse = Course & WithStudentRole & WithRate & WithLevel

//   Student,    User   .
type Student = User & { courses: { [id: number]: StudentCourse } }

//   Teacher,    User, WithLevel   .
type Teacher = User & WithLevel & { courses: { [id: number]: Course & WithTeacherRole } }

//   ,   -  ,  -  User.
type StudentDict = { [id: string]: User };

//   ,   -  ,  -  User    .
type TeacherDict = { [id: string]: User & WithLevel & WithRate }

//   Director,    User     .
type Director = User & { students: StudentDict } & { teachers: TeacherDict }

/*--    --*/

//   s1,    Student.
const s1: Student = {
    id: "s1",
    name: "s1",
    courses: {
        [1]: {
            id: 1,
            title: "First",
            rate: 5,
            role: "student",
            level: "middle"
        }
    },
}

//   t1,    Teacher.
const t1: Teacher = {
    id: "t1",
    name: "t1",
    level: "junior",
    courses: {
        [5]: {
            id: 5,
            title: "Fifth",
            role: "teacher" //  
        },
        [1]: {
            ...s1.courses[1], //       "teacher"
            role: "teacher"
        }
    }
}

//   d1,    Director.
const d1: Director = {
    id: "d1",
    name: "d1",
    students: {
        ["s1"]: s1, //  s1
        ["s2"]: {
            id: "s2",
            name: "s2" //  
        }
    },
    teachers: {
        ["t1"]: {
            ...t1,
            rate: 3, //   t1
        },
        ["t2"]: {
            id: "t2",
            name: "t2",
            level: "senior",
            rate: 5 //   t2
        }
    }
}
`,
