import { useSelector } from "react-redux";
import { Flex, Button, List, Card } from "antd";
import { useNavigate } from "react-router-dom";

/*
gutter:   ( )   .
xs:   ,       ( XS,   576 ).
sm:   ,       ( SM,  576-767 ).
md:   ,       ( MD,  768-991 ).
lg:   ,       ( LG,  992-1199 ).
xl:   ,        ( XL,  1200-1599 ).
xxl:   ,       ( XXL,   1600 ).
*/

export default function MainContent() {
	const listTopic = useSelector((state) => state.topic.topic);
	const navigate = useNavigate();

	const data = [
		{
			title: "",
			description: (
				<Flex wrap gap="small">
					{listTopic
						.filter((item) => item.key < 12)
						.map((i) => (
							<Button
								onClick={() => navigate(`/hook/${i.label}`)}
								key={i.key}
								type="primary"
							>
								{i.label}
							</Button>
						))}
				</Flex>
			),
		},
		{
			title: "",
			description: (
				<Flex wrap gap="small">
					{listTopic
						.filter((item) => item.key >= 12 && item.key < 16)
						.map((i) => (
							<Button
								onClick={() => navigate(`/dop/${i.label}`)}
								key={i.key}
								type="primary"
							>
								{i.label}
							</Button>
						))}
				</Flex>
			),
		},
		{
			title: "",
			description: (
				<Flex wrap gap="small">
					{listTopic
						.filter((item) => item.key >= 16)
						.map((i) => (
							<Button
								onClick={() => navigate(`/dop/${i.label}`)}
								key={i.key}
								type="primary"
							>
								{i.label}
							</Button>
						))}
				</Flex>
			),
		},
	];

	return (
		<List
			grid={{
				gutter: 16,
				xs: 1,
				sm: 1,
				md: 1,
				lg: 1,
				xl: 1,
				xxl: 1,
			}}
			dataSource={data}
			renderItem={(item) => (
				<List.Item>
					<Card title={item.title}>{item.description}</Card>
				</List.Item>
			)}
		/>
	);
}
