import { Button, Divider, List } from "antd";
import Link from "antd/es/typography/Link";
import Typography from "antd/es/typography/Typography";

const { Text } = Typography;

const data = [
	{
		title: "pathname:",
		content:
			",    URL. (:  http://example.com/about -> /about)",
	},
	{
		title: "search:",
		content:
			",     URL (  ?). (:  http://example.com/about?name=John&age=30 -> ?name=John&age=30)",
	},
	{
		title: "hash:",
		content:
			",   ( URL,   #). (:  http://example.com/about#section2 -> #section2)",
	},
	{
		title: "state:",
		content:
			",  ,     (,   history.push  history.replace). (:  history.push('/about', { from: 'Home' }) -> { from: Home' }",
	},
	{
		title: "key:",
		content:
			",     location,       . (:  http://example.com/about -> 'ac3df4')",
	},
];

const use = [
	"       URL.",
	"       URL.",
	"    ()  URL.",
	"   ,   navigate.",
	"    URL      .",
];

export default function UseLocationTheory() {
	return (
		<>
			<Typography.Title level={3}></Typography.Title>
			<Typography.Paragraph style={{ textAlign: "left" }}>
				<Text code>useLocation</Text>  , 
				 URL.       ,
				    .  useLocation 
				 location,    pathname, search,
				hash, state  ,    
				.
				<br />
				  location pathname:
			</Typography.Paragraph>
			<List
				size="small"
				bordered
				title=""
				dataSource={data}
				renderItem={(item) => (
					<List.Item>
						<List.Item.Meta
							title={item.title}
							description={item.content}
						/>
					</List.Item>
				)}
			/>
			<br />
			<Link
				target="_blanks"
				href="https://my-js.org/docs/guide/react-router/#uselocation"
			>
				<Button type="primary"></Button>
			</Link>
			<Divider />
			<Typography.Title level={3}> </Typography.Title>
			<List
				size="small"
				bordered
				dataSource={use}
				renderItem={(item) => <List.Item>{item}</List.Item>}
			/>
		</>
	);
}
