import { Button, Divider, List } from "antd";
import Link from "antd/es/typography/Link";
import Typography from "antd/es/typography/Typography";

const { Text } = Typography;

const data = [
	{
		title: "  DOM-:",
		content:
			"useRef        DOM- .  ,        ,    document.querySelector   JavaScript.",
	},
	{
		title: "  :",
		content:
			"useRef      ,   ,       . ,   ,    ,     ,     .",
	},
	{
		title: "   :",
		content:
			"   current ,  useRef,     .    : ,   useRef,    .",
	},
];

const use = [
	"     DOM-     .",
	"    ,      .",
];

export default function UseRefTheory() {
	return (
		<>
			<Typography.Title level={3}></Typography.Title>
			<Typography.Paragraph style={{ textAlign: "left" }}>
				 <Text code>useRef()</Text>  , 
				current      DOM.   
				      .
				 : <Text code>const node = useRef()</Text>.
				 :{" "}
				<Text code>{`<tagName ref={node}></tagName>`}</Text>.
			</Typography.Paragraph>
			<List
				size="small"
				bordered
				title=""
				dataSource={data}
				renderItem={(item) => (
					<List.Item>
						<List.Item.Meta
							title={item.title}
							description={item.content}
						/>
					</List.Item>
				)}
			/>
			<br />
			<Typography.Paragraph style={{ textAlign: "left" }}>
				useRef   ref ,   
				current    
				 (initialValue).    
				    .
			</Typography.Paragraph>
			<Typography.Paragraph style={{ textAlign: "left" }}>
				         useEffect, 
				    ,    
				useEffect,  useRef    (   ,
				  )
			</Typography.Paragraph>
			<Link
				target="_blanks"
				href="https://my-js.org/docs/cheatsheet/react-hooks/#useref"
			>
				<Button type="primary"></Button>
			</Link>
			<Divider />
			<Typography.Title level={3}> </Typography.Title>
			<List
				size="small"
				bordered
				dataSource={use}
				renderItem={(item) => <List.Item>{item}</List.Item>}
			/>
		</>
	);
}
