import { Divider, List, Button } from "antd";
import Typography from "antd/es/typography/Typography";
import Link from "antd/es/typography/Link";

const { Text } = Typography;

const data = [
	{
		title: "to",
		content: "  ,    .",
	},
	{
		title: "options",
		content: "    .",
	},
];

const examples = [
	{
		title: "   :",
		content: "navigate('/home');",
	},
	{
		title: "     (replace):",
		content: "navigate('/home', { replace: true });",
	},
	{
		title: "    (state):",
		content: "navigate('/home', { state: { fromDashboard: true } });",
	},
	{
		title: "  (   ):",
		content: "navigate(-1); ",
	},
	{
		title: "  (   ):",
		content: "navigate(1);",
	},
];

const use = [
	"          .",
	"        .",
	"      - .",
	"        .",
];

export default function UseNavigateTheory() {
	return (
		<>
			<Typography.Title level={3}></Typography.Title>
			<Typography.Paragraph style={{ textAlign: "left" }}>
				<Text code>useNavigate</Text>   
				     .  
				,       ,
				  history push  history.replace   
				React Router.
				<Typography.Title level={5}>
					,  useNavigate,   
					:
				</Typography.Title>
				<List
					size="small"
					bordered
					title=""
					dataSource={data}
					renderItem={(item) => (
						<List.Item>
							<List.Item.Meta
								title={item.title}
								description={item.content}
							/>
						</List.Item>
					)}
				/>
				<Typography.Title level={5}>
					  useNavigate:
				</Typography.Title>
			</Typography.Paragraph>
			<List
				size="small"
				bordered
				title=""
				dataSource={examples}
				renderItem={(item) => (
					<List.Item>
						<List.Item.Meta
							title={item.title}
							description={item.content}
						/>
					</List.Item>
				)}
			/>
			<br />
			<Link
				target="_blanks"
				href="https://ru.hexlet.io/blog/posts/react-router-v6"
			>
				<Button type="primary"></Button>
			</Link>
			<Divider />
			<Typography.Title level={3}> </Typography.Title>
			<List
				size="small"
				bordered
				dataSource={use}
				renderItem={(item) => <List.Item>{item}</List.Item>}
			/>
		</>
	);
}
